const state={
    page:{
        titleData: [], //表格标题数组
        datas: [], //表格数据数组
        pages: [], //页码数组
        pageselect: [2, 5, 10], //可选择每页显示条数数组
        active: 1, //当前页
        pagelist: 5, //每页显示数据条数
        pageTotal: 0, //总页数
        listTotal: 0, //总条数
        count: 5, //页码可显示数，即页码数组长度
        checkData: []
    }
}
const mutations={
    UPDATEPAGEACTIVE(state,val){
        state.page.active=val;
    },
    UPDATEPAGETOTAL(state,val){
        state.page.pageTotal=val;
    },
    UPDATETITLEDATA(state,val){
        state.page.titleData=val;
    },
    UPDATEDATAS(state,val){
        state.page.datas=val;
    },
    UPDATEPAGES(state,val){
        state.page.pages=val;
    },
    UPDATEPAGELIST(state,val){
        state.page.pagelist=val;
    },
    UPDATELISTTOTAL(state,val){
        state.page.listTotal=val;
    },
    UPDATECOUNT(state,val){
        state.page.count=val;
    },
    UPDATECHECKDATA(state,val){
        state.page.checkData=val;
    }
}
const actions={
    REININTPAGE(state){
        state.page={
            titleData: [], //表格标题数组
            datas: [], //表格数据数组
            pages: [], //页码数组
            pageselect: [2, 5, 10], //可选择每页显示条数数组
            active: 1, //当前页
            pagelist: 5, //每页显示数据条数
            pageTotal: 0, //总页数
            listTotal: 0, //总条数
            count: 5, //页码可显示数，即页码数组长度
            checkData: []
        };
    }
}
const getters={
    GETUSERTABLEPAGE(state){
        return state.page;
    },
    GETUSERTABLEPAGECHECKDATA(state){
        return state.page.checkData;
    }
}
const page={
    state,
    mutations,
    actions,
    getters
}
export default page;


