const http = {
  loginTableData(vm) {
    var pages = vm.$store.getters.GETUSERTABLEPAGE;
    console.log(pages)
    var data = {
      page: {
        active: pages.active,
        pagelist: pages.pagelist
      },
      state: vm.pageloginData
    }

    vm.$axios
      .post(vm.HOST + "selectPaging.action", data)
      .then(function (success) {
        var res = success.data;
        layer.msg("数据加载中", {
          icon: 16,
          time:200,
          end: function () {
            if (res.code == "0") {
              vm.checkEmpty();
              vm.$store.commit("UPDATETITLEDATA", vm.judgeTitle(res.data.data_title));
              vm.$store.commit("UPDATELISTTOTAL", res.data.listTotal);
              vm.$store.commit("UPDATEDATAS", res.data.lists);
              vm.$store.commit("UPDATEPAGETOTAL", Math.ceil(pages.listTotal / pages.pagelist));
              vm.$store.commit("UPDATEPAGES", vm.pagelogin(
                pages.pageTotal,
                pages.active,
                pages.count
              ));
            } else {
              layer.msg("获取失败", {
                icon: 5
              });
            }
          }
        });
      })
      .catch(function (error) {
        console.log(error);
        vm.$store.commit("UPDATEDATAS", []);
        layer.msg("网络错误，请检查网络是否畅通", {
          icon: 5
        });
      });
    layer.closeAll("loading");
  },
  opreateHttp(vm, url, data, opreatetitle) {
    vm.$validator.validateAll().then(function (value) {
      if (value) {
        vm.$axios
          .post(vm.HOST + url, data)
          .then(function (success) {
            var res = success.data;
            if (res.code == "0") {
              vm.$layer.msg(
                opreatetitle +
                "成功", {
                  icon: 1,
                  end: function () {
                    http.loginTableData();
                  }
                });
              data = {};
            } else {
              vm.$layer.msg(res.msg, {
                icon: 2
              });
            }
          })
          .catch(function (error) {
            vm.$layer.msg(
              '网络错误，请检查网络是否畅通', {
                icon: 5
              }
            );
          });
      }
    });
  },
}

export default http;



